import React, { Component } from 'react'

export default class header extends Component{
    render(){
        return (
            <>
            {/* header
                ================================================== */}
            <header className="s-header">
                <div className="row s-header__nav-wrap">
                <nav className="s-header__nav">
                    <ul>
                    <li className="current"><a className="smoothscroll" href="#hero">Inicio</a></li>
                    <li><a className="smoothscroll" href="#about">Acerca de mi</a></li>
                    <li><a className="smoothscroll" href="#resume">Currículum</a></li>
                    <li><a className="smoothscroll" href="#portfolio">Proyectos</a></li>
                    <li><a className="smoothscroll" href="#testimonials">Testimonios</a></li>
                    <li><a className="smoothscroll" href="#contact">Contacto</a></li>
                    </ul>
                </nav>
                </div> {/* end row */}
                <a className="s-header__menu-toggle" href="#0" title="Menu">
                <span className="s-header__menu-icon" />
                </a>
            </header> {/* end s-header */}
            {/* hero
                ================================================== */}
            <section id="hero" className="s-hero target-section">
                <div className="s-hero__bg rellax" data-rellax-speed={-7} />
                <div className="row s-hero__content">
                <div className="column">
                    <div className="s-hero__content-about">
                    <h1>Soy Joaquín Herrera</h1>
                    <h3>
                        Un <span>programador</span> y <span>desarrollador back-end</span>.
                    </h3>
                    <div className="s-hero__content-social">
                        <a href="#0"><i className="fab fa-facebook-square" aria-hidden="false" /></a>
                        <a href="#0"><i className="fab fa-twitter" aria-hidden="false" /></a>
                        <a href="#0"><i className="fab fa-instagram" aria-hidden="false" /></a>
                        <a href="#0"><i className="fab fa-dribbble" aria-hidden="false" /></a>
                        <a href="#0"><i className="fab fa-behance" aria-hidden="false" /></a>
                        <a href="#0"><i className="fab fa-linkedin" aria-hidden="false" /></a>
                    </div>
                    </div> {/* end s-hero__content-about */}
                </div>
                </div> {/* s-hero__content */}
                <div className="s-hero__scroll">
                <a href="#about" className="s-hero__scroll-link smoothscroll">
                    <span className="scroll-arrow">
                    <svg xmlns="http://www.w3.org/2000/svg" width={32} height={32} viewBox="0 0 24 24" style={{fill: 'rgba(0, 0, 0, 1)', transform: '', msFilter: ''}}><path d="M18.707 12.707L17.293 11.293 13 15.586 13 6 11 6 11 15.586 6.707 11.293 5.293 12.707 12 19.414z" /></svg>
                    </span>
                </a>
                </div> {/* s-hero__scroll */}
            </section> {/* end s-hero */}
            </>

        )
    }
}